.btn {
  font-family: "Poppins", sans-serif;
  text-transform: capitalize;
  padding: 27px 44px !important;
  color: #fff;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  line-height: 1;
  -moz-user-select: none;
  letter-spacing: 1px;
  line-height: 0;
  margin-bottom: 0;
  cursor: pointer;
  transition: color 0.4s linear;
  position: relative;
  z-index: 1;
  border: 0;
  overflow: hidden;
  margin: 0;
  border-radius: 30px !important;
  display: inline-block !important;
}
.btn::after {
  position: unset !important;
}
.btn::before {
  content: "";
  position: absolute !important;
  left: 0;
  top: 0;
  width: 101%;
  height: 101%;
  z-index: 1;
  border-radius: 5px;
  transition: transform 0.5s;
  transition-timing-function: ease;
  transform-origin: 0 0;
  transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
  transform: scaleX(0);
  border-radius: 0px;
}
.btn i {
  padding-right: 11px;
}
.btn:hover {
  color: #fff !important;
  text-decoration: none !important;
}
.btn:hover::before {
  transform: scaleX(1);
  color: #fff !important;
  z-index: -1;
}
.btn.focus,
.btn:focus {
  outline: 0;
  box-shadow: none;
}
